import * as actionTypes from './actionTypes'

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}

export const addNewButtle = (buttle) => {
  return {
    type: actionTypes.ADD_NEW_BUTTLE,
    payload: {
      buttle
    }
  }
}

export const deleteButtle = (buttle) => {
  return {
    type: actionTypes.DELETE_BUTTLE,
    payload: {
      buttle
    }
  }
}

export const addNewEnemy = (key, enemy) => {
  return {
    type: actionTypes.ADD_NEW_ENEMY,
    payload: {
      key,
      enemy
    }
  }
}

export const deleteEnemy = (key, enemy, byUser = false) => {
  return {
    type: actionTypes.DELETE_ENEMY,
    payload: {
      key,
      enemy,
      byUser
    }
  }
}

export const updateEnemy = (key, enemy) => {
  return {
    type: actionTypes.UPDATE_ENEMY,
    payload: {
      key,
      enemy
    }
  }
}

export const toggleHitUser = (isHitUser, hitPoint = 10) => {
  return {
    type: actionTypes.TOGGLE_HIT_USER,
    payload: {
      isHitUser,
      hitPoint
    }
  }
}

export const addNewEnemyButtle = (buttle) => {
  return {
    type: actionTypes.ADD_NEW_ENEMY_BUTTLE,
    payload: {
      buttle
    }
  }
}

export const deleteEnemyButtle = (buttle) => {
  return {
    type: actionTypes.DELETE_ENEMY_BUTTLE,
    payload: {
      buttle
    }
  }
}

export const updateShield = (shield) => {
  return {
    type: actionTypes.UPDATE_SHIELD,
    payload: {
      shield
    }
  }
}

export const restartGame = () => {
  return {
    type: actionTypes.RESTART_GAME
  }
}

export const toggleModal = () => {
  return {
    type: actionTypes.TOGGLE_MODAL
  }
}

export const updatePlayer = (player) => {
  return {
    type: actionTypes.UPDATE_PLAYER,
    payload: {
      player
    }
  }
}



