import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import RankContent from '../component/Rank/RankContent'

class Rank extends Component {
  componentDidMount () {
    const {setDataLoaded, getRankList} = this.props
    setDataLoaded(false)
    getRankList()
  }

  render () {
    return (
      <main>
        <RankContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    isDataLoaded: state.isDataLoaded,
    rankList: state.rankList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getRankList: () => dispatch(actionCreators.getRankList()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Rank)



