import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_USER_INFO,
          userInfo: data
        })
      })
  }
}

export const getRankList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/getRankList`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_RANK_LIST,
          payload: {
            rankList: data.result
          }
        })
      })
  }
}

export const addNewRank = (rank) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/addNewRank`, {
      method: 'post',
      body: JSON.stringify(rank)
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.ADD_NEW_RANK,
          payload: {
            rankList: data.result
          }
        })
      })
  }
}



