import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

import StarIcon from '@material-ui/icons/Star'

import yellow from '@material-ui/core/colors/yellow'
import grey from '@material-ui/core/colors/grey'
import blueGrey from '@material-ui/core/colors/blueGrey'
import deepPurple from '@material-ui/core/colors/deepPurple'

const styles = theme => ({
  root: {
    background: `linear-gradient(to right, black, ${deepPurple[500]}, black)`
  },
  whiteText: {
    color: '#fff'
  },
  message: {
    letterSpacing: '1rem',
    margin: '5rem'
  },
  rankItem: {
    padding: '1rem',
    margin: '1rem'
  },
  first: {
    border: `2px solid ${yellow[500]}`
  },
  second: {
    border: `2px solid ${grey[500]}`
  },
  third: {
    border: `2px solid ${blueGrey[500]}`
  },
  other: {
    border: `2px solid #ccc`
  },
  floatRight: {
    float: 'right'
  },
  toolbar: theme.mixins.toolbar
})

class RankContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const {classes, rankList, isDataLoaded} = this.props
    const [firstRank, secondRank, thirdRank, ...otherRank] = rankList
    console.log(rankList)

    return (
      <div>
        {
          isDataLoaded && (rankList.length > 0) &&
          <div className={classes.root}>
            <div className={classes.toolbar} />
            <Grid container justify='center'>
              <Grid item container xs={8} alignItems='center'>
                <Grid item xs={12} className={`${classes.rankItem} ${classes.first}`}>
                  <Typography variant='headline' className={`${classes.whiteText}`}>
                    <StarIcon style={{color: yellow[500]}} />&nbsp;&nbsp;
                    {firstRank.player}
                    <span className={classes.floatRight}>擊殺數 {firstRank.totalKill}</span>
                  </Typography>
                </Grid>
                <Grid item xs={12} className={`${classes.rankItem} ${classes.second}`}>
                  <Typography variant='headline' className={`${classes.whiteText}`}>
                    <StarIcon style={{color: grey[500]}} />&nbsp;&nbsp;
                    {secondRank.player}
                    <span className={classes.floatRight}>擊殺數 {secondRank.totalKill}</span>
                  </Typography>
                </Grid>
                <Grid item xs={12} className={`${classes.rankItem} ${classes.third}`}>
                  <Typography variant='headline' className={`${classes.whiteText}`}>
                    <StarIcon style={{color: blueGrey[500]}} />&nbsp;&nbsp;
                    {thirdRank.player}
                    <span className={classes.floatRight}>擊殺數 {thirdRank.totalKill}</span>
                  </Typography>
                </Grid>
                {
                  otherRank.map(rank => (
                    <Grid
                      item
                      xs={12}
                      className={`${classes.rankItem} ${classes.other}`}
                      key={rank.timestamp}>
                      <Typography variant='subheading' className={`${classes.whiteText}`}>
                        {rank.player}
                        <span className={classes.floatRight}>擊殺數 {rank.totalKill}</span>
                      </Typography>
                    </Grid>
                  ))
                }
              </Grid>
            </Grid>
          </div>
        }
        {
          !isDataLoaded &&
          <div className={classes.message}>
            <div className={classes.toolbar} />
            <Typography
              variant='title'
              align='center'
              className={classes.whiteText}>
              排行計算中
            </Typography>
          </div>
        }
      </div>
    )
  }
}

export default withStyles(styles)(RankContent)



